import { TApiResponse } from './types/types';

//     
export const checkStatus = (res: TApiResponse) => {
  if (res.ok) {
    return res.json();
  }
    if (res.status === 401) {
      return Promise.reject(` ${res.status}.   `);
    } else if (res.status === 403) {
      return Promise.reject(` ${res.status}.       `);
    } else {
      return Promise.reject(` ${res.status}.    .  `);
    }

}